* {
  background-color: aquamarine;
}

.title {
  font-size: 5rem;
  margin-bottom: 1em;
  text-align: center;
}
.list-container {
  display: flex;
  flex-direction: column;
  gap: 1em;
  margin-left: .5em;
  font-size: 2em;
  margin-bottom: 2em;
}
.checked-item,
.not-checked-item {
  margin-left: .5em;
}
.checked-item {
  text-decoration: line-through;
  text-decoration-color: hotpink;
}
.expired-notice {
  text-align: center;
  padding: 2rem;
  border: 1px solid #ebebeb;
  border-radius: 0.25rem;
  margin: 0.5rem;
}

.expired-notice > span {
  font-size: 2.5rem;
  font-weight: bold;
  color: hotpink;
}

.expired-notice > p {
  font-size: 1.5rem;
}

.show-counter {
  padding: 0.5rem;
}

.show-counter .countdown-link {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  font-weight: 700;
  font-size: 1.25rem;
  line-height: 1.75rem;
  padding: 0.5rem;
  border: 2px solid #ebebeb;
  border-radius: 0.25rem;
  text-decoration: none;
  color: #000;
}

.show-counter .countdown {
  line-height: 1.25rem;
  padding: 0 0.75rem 0 0.75rem;
  align-items: center;
  display: flex;
  flex-direction: column;
}

.show-counter .countdown.danger {
  color: hotpink;
}

.show-counter .countdown > p {
  margin: 0;
}

.show-counter .countdown > span {
  text-transform: uppercase;
  font-size: 0.75rem;
  line-height: 1rem;
}

.counting, .itemCheck {
  text-align: center;

}